// Variables
  $font-primary: Helvetica, arial, sans-serif;
  $lithios-primary: blue;

// Variables in use
  body {
    font: 100% $font-primary;  
  }

  h1,h2,h3,h4,h5{
    color: $lithios-primary;
    font: $font-primary;  
  }


// .SCSS Format
  .people {
    border: solid 1px $lithios-primary;
    .row {      
     .col-md-4 {
        padding: 100px 0px;
        h2{
          text-transform: capitalize;
          color: lighten($lithios-primary, 35%);
        }  
      }    
    }
   



// Extended Class 
  .teal-background{
    background: $lithios-primary;
    padding: 20px;    
  }

// Extended Class In Use & Color Darken & Lighten
  .widget{
    min-height: 100px;
    @extend .teal-background;
    h2{
      color: darken($lithios-primary, 15%);
      &:hover{
        color: lighten($lithios-primary, 25%);
      }
      &:active{

      }
    }
   }

    
  


    // This does the same thing with less code in SASS
    @each $name in deshawn, tomer, arjun, tim, rob, maddie, sareena {
      .people-#{$name} {
        background-image: url('../../img/#{$name}.jpg');
      }
    }
    .people-rob-h2{    	
    	color: blue;    	
    }





  }


   .btn{
   		border-radius:0px;	
   		&-default{
   			background-color:blue;
   		}
   		&-primary{
   			background-color:red;
   		}
   }







